
*{margin: 0;padding: 0;font-size:0.25rem;line-height:0.24rem;
}
body{font-family:Arial, "宋体","Microsoft YaHei";
}
ul{ list-style:none; overflow: hidden;
}	/*溢出隐藏*/	/*去本身样式       */  /*图片的边框*/
a{text-decoration:none;vertical-align:top;color: #666666;
}/*参考对齐上*/
a:hover{color: #C81623;
}
i{font-style: normal;font-family: consolas;
}/*特殊字体	取消斜体*//*a的下划线去掉*/
img,input{border:none;
}/*图片的边框*/
.clearfix:after{
	content: '.';
	display: block;
	visibility: hidden;
	height: 0;
	clear: both;
}
.clearfix{
	*zoom: 1;
}
.left{
	float:left;
}
.you{
	float:right;
}
#app {
  font-family: 'Avenir','Noto', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}



