
.hotspotWrap{border-bottom:1px solid #ccc;padding:0.33rem 0 0.4rem 0.4rem;background: #fff;
}
.hotspotWrap .himg{width:100%;
}
.hotspotWrap .left{width:4.22rem;float: left;
}
.hotspotWrap .left h2{font-size:0.38rem;line-height:0.45rem;
}
.hotspotWrap .left p{padding-top:0.16rem;color:#a5a5a5;font-size:0.25rem;line-height:0.36rem;
}
.hotspotWrap .rightImg{float:right;margin:0.07rem 0.38rem 0 0;
}
.hotspotWrap .rightImg img{width:1.73rem;height:1.73rem;
}
.hotspotWrap i{display:block;width:0.28rem;height:0.02rem;background:#d8d8d8;margin:0.15rem 0 0.2rem 0;
}
.hotspotWrap .comeFrom{float: right;color:#cccccc;font-size:0.18rem;
}
.hotspotWrap .bottom p{color:#cccccc;font-size:0.25rem;margin-right:0.38rem;
}
.hotspotWrap .bottom .author span{color:#cccccc;font-size:0.25rem;
}




